.typer{
    display: flex;
    justify-content: center;
    align-items: center;
    height:100vh;
    background-image:url(/React-PortFolio/static/media/1.eea16b24.jpg);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    width:100%;
  
    
}

.typer-text{
    font-family: 'Noto Sans KR', sans-serif;
    border-right: 0.1em solid white;
    font-size: 4vw;
    white-space: pre;
    line-height: 1;
    letter-spacing: 0.1rem;
    padding-top: 0.2em;
    -webkit-animation: blink 1s step-end infinite;
            animation: blink 1s step-end infinite;
}
.about_me{
    
    display: flex;
    justify-content:flex-start;
    margin: 0;
    background:white;
    height:100%;   
    width: 100%;
 
}



.about_me_title{
    margin-left: 6vw;
   margin-right: 12vw;
    width: 25vw;
  box-sizing: border-box;
    text-align: center;
    align-self: center;
    
}
.hedding{
    position: relative;
    display: inline-block;
    font-size: 2.4em;
    font-weight:400;
    margin:30px 0;
}
.hedding::after{
    position: absolute;
    content: "";
    top:100%;
    height:2px;
    width: 50px;
    left: 0;
    right: 0;
    margin:0 auto;
    background:rgb(123, 124, 212);
}

.about_me_content{
    font-family: 'Noto Sans KR', sans-serif;
    margin-bottom: 4rem;
    margin-top: 4rem;
    box-sizing: border-box;
padding: 1rem;
    margin-right:8vw;
    width: 100%;
}
@media (max-width: 576px) { 
    .about_me{
        display:inherit;
        height: 100%;
        width: 100%;
        text-align: center;
      
    }   
    .about_me_title{
        display: inline-block;
      text-align: center;
      margin-left: 0;
      margin-right: 0;
    }
    .about_me_content{
        display: inline-block;
      margin-top: 0px;
       width:100%;
    }
     }  

.Row{
    display: flex;
    flex-wrap: wrap;
    margin-right:-15px;
    margin-left:-15px;
}
.Box{
    height: 360px;
    
    width: 100%;
    max-width: 80vw;
    border-radius: 4px;
    margin:0 auto 30px;
    background: #ffffff;
    box-shadow: 0 3px 6px rgba(0,0,0,.2), 0 3px 6px rgba(0,0,0,.2);
}
.Box_image{
    float: left;
    width:  35%;
    height:360px;
    margin-right:2rem;
}
.image{
   width: 100%;
   height:360px;
}
.Box_info{
    font-family: 'Gothic A1', sans-serif;
    padding:14px;
}
.Box_title{
    font-family: 'Noto Sans TC', sans-serif;
    font-weight:500;
    font-size:9px;
    text-align: center;
}
.Box_content{
    font-size: 1.4em;
}
.name{
    font-weight:500;
    font-size: 1.4em;
}
.Box_ul{
    list-style: none;
  
}
.Box_li{
    display: inline-block;
    cursor: pointer;
}
.icon_image{
    width: 25px;
    height:30px;
}
@media (max-width: 576px){
    .Row{
        font-size: 10px;
        width: 100%;
    }
    .Box{
        width: 100%;
        max-width: 100%;
        height:240px;
    }
    .Box_image{
        float: left;
        height:240px;
        margin-right:2rem;
    }
    .image{
       width: 100%;
       height:240px;
    }
    
}
@media (max-width: 320px){
    .Row{
        font-size: 7px;
        width: 100%;
    }
    .Box{
        width: 100%;
        max-width: 100%;
        height: 180px;
    }
.Box_image{
    float: left;
   
    height:180px;
    margin-right:2rem;
}
.image{
   width: 100%;
   height:180px;
}

}
